.catalog {
  &__wrapper {
    @include container();
    padding-bottom: 60px;
  }

  &__path {
    @include path();
  }

  &__title {
    font-family: $bold;
    font-size: $prs*3;
    line-height: $prs*4.1;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin-bottom: 20px;
    color: $black;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    max-width: 555px;
    padding: 10px 0;
    padding-right: 40px;
    margin-right: auto;
    margin-bottom: 30px;
    border: 1px solid $lightgrey;
    background: $white;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  &__link {
    font-size: $prs*0.9;
    line-height: $prs*1.2;
    text-decoration-line: underline;
    align-self: flex-end;

    color: $lightblue;
  }

  &__pagination {
    border-top: 1px solid $catalog-brd;

    &-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 10px;
    }

    &-btn {
      width: 150px;
      font-size: $prs*1.6;
      line-height: $prs*2.8;
      min-height: 40px;
      border: none;
      padding: 0 10px;
      margin-bottom: 10px;
      cursor: pointer;
      border-radius: 5px;

      background-color: $white;
      color: $black;
      @include hover($yellow, $black);
    }
  }

  &__article {
    display: none;
  }
}


@media (max-width: $desktop-width) {
  .catalog {
    &__wrapper {
      padding-top: 45px;
      padding-bottom: 30px;
    }

    &__title {
      margin-bottom: 30px;
    }

    &__list {
      padding-bottom: 0;
    }

    &__item {
      width: 47.5%;
      max-width: 47.5%;
      margin-right: 5%;
      margin-bottom: 20px;
    }

    &__article {
      display: block;

      &-wrapper {
        @include container();
        display: flex;
        flex-direction: column;
        padding-top: 40px;
      }

      &-title {
        font-family: $bold;
        font-size: $prs*2.4;
        line-height: $prs*4.2;
        margin: 0 auto;
        margin-bottom: 45px;

        color: $black;
      }

      &-text {
        font-size: $prs*1.6;
        line-height: $prs*2.8;
        margin-bottom: 40px;

        color: $black;
      }

      picture {
        width: 100%;
        height: 248px;
        margin-bottom: 10px;

        img {
          width: inherit;
          height: inherit;
          object-fit: cover;
        }
      }

      strong {
        font-family: $bold;
        font-size: $prs*1.6;
        line-height: $prs*2.8;
        margin-bottom: 10px;

        color: $black;
      }

      ul {
        margin-bottom: 30px;

        li {
          padding-left: 40px;
          margin-bottom: 10px;
        }
      }
    }
  }
}

@media (max-width: $tablet-width) {
  .catalog {
    &__wrapper {
      padding-bottom: 0;
    }

    &__title {
      font-size: $prs*2.4;
      line-height: $prs*3.3;
      padding-bottom: 30px;
    }

    &__list {
      flex-direction: column;
      border: none;
    }

    &__item {
      width: 100%;
      max-width: 100%;
      margin-right: 0;
    }

    &__article {
      display: none;
    }

    &__pagination {
      &-btn {
        min-height: 30px;
      }
    }
  }
}
